






























































































































import { Vue, Component, Watch, Prop } from "vue-property-decorator";
import AddMenu from "./components/AddMenu.vue";
import MenuFuncManage from "./components/MenuFuncManage.vue";

import Url from "../../api";
import utils from "@/utils/utils";

@Component({
  components: {
    AddMenu,
    MenuFuncManage
  }
})
export default class SystemMenu extends Vue {
  treeData = [];
  // 配置选项
  defaultProps = {
    children: "children",
    label: "name"
  };
  currentMenuId = "";
  currentMenuName = "";
  // 选中表单信息
  basicForm: any = {};
  functionTableData: Array<any> = [];

  // 弹窗表单信息
  dialogTitle: string = "";
  visibleDialog: boolean = false;
  dialogBasicForm: any = {
    id: "",
    parentId: null,
    parentName: "",
    name: "",
    icon: "",
    code: "",
    innerRoute: true,
    path: "",
    target: "_self",
    remark: ""
  };
  openOption: Array<any> = [
    { id: "_blank", label: "新窗口打开" },
    { id: "_self", label: "当前窗口打开" }
    // { id: "framename", label: "指定位置打开" }
  ];

  created() {
    // this.getSystemData();
    this.getTypeTreeData();
  }

  // 获取所属系统列表
  // getSystemData() {
  //   this.$http.get(Url.getSubsystemList).then((res: any) => {
  //     if (res.data.status) {
  //       this.sysOptions = res.data.data;
  //     } else {
  //       this.$message.error(res.data.message);
  //     }
  //   });
  // }

  // 获取树数据
  getTypeTreeData() {
    this.$http.get(Url.getMenuList).then((res: any) => {
      if (res.data.status) {
        this.treeData = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  // 树节点点击事件
  handleNodeClick(data: any) {
    // console.log(data);
    this.currentMenuId = data.id;
    this.currentMenuName = data.name;
    this.getCustomAttributeList(data.id);
  }
  // 根据节点ID获取详细信息
  getCustomAttributeList(id: any) {
    this.$http.get(Url.getMenuDetail + "/" + id).then((res: any) => {
      if (res.data.status) {
        this.basicForm = res.data.data;
        this.basicForm.innerRoute = this.basicForm.innerRoute ? "是" : "否";
        // let i = this.sysOptions.findIndex((li: any) => li.code === this.basicForm.subsystemCode);
        // this.basicForm.subName = this.sysOptions[i].subsystemName;
        let j = this.openOption.findIndex((li: any) => li.id === this.basicForm.target);
        this.basicForm.targetName = this.openOption[j].label;
        this.functionTableData = res.data.data.navigationOperations ? res.data.data.navigationOperations : [];
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  // 打开节点弹窗
  openTypeTreeNodeForm(buttonName: any, data: any) {
    switch (buttonName) {
      case "add":
        this.dialogTitle = "添加菜单";
        this.dialogBasicForm = {
          id: "",
          parentId: data.id,
          parentName: data.name,
          name: "",
          icon: "",
          code: "",
          innerRoute: true,
          path: "",
          target: "_self",
          remark: "",
          level: data.level + 1
        };
        break;
      case "edit":
        this.dialogTitle = "编辑菜单";
        utils.mapValue(this.dialogBasicForm, data);
        break;
      case "addTop":
        this.dialogTitle = "添加一级菜单";
        this.dialogBasicForm = {
          id: "",
          parentId: null,
          parentName: "",
          name: "",
          icon: "",
          code: "",
          innerRoute: true,
          path: "",
          target: "_self",
          remark: "",
          level: 1
        };
        break;
    }
    this.visibleDialog = true;
  }
  // 保存树节点
  saveTypeAndAttr() {
    this.visibleDialog = false;
    this.getTypeTreeData();
    if (this.currentMenuId) {
      this.getCustomAttributeList(this.currentMenuId);
    } else {
      this.basicForm = {};
    }
  }
  // 删除节点
  remove(node: any, data: any) {
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(`${Url.deleteMenu}${data.id}`).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message.success("操作成功");
              this.getTypeTreeData();
            } else {
              this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }

  async handleMove(node: any, $data: any, direction: number) {
    let {
      data: { data, status, message }
    } = await this.$http.post(Url.moveMenuOrder + $data.id, null, {
      params: {
        direction
      }
    });
    if (status) {
      this.$message.success("操作成功");
      this.getTypeTreeData();
    } else {
      this.$message.error(message);
    }
  }

  closeDialog() {
    this.visibleDialog = false;
  }

  getFuncData() {
    this.getCustomAttributeList(this.currentMenuId);
  }
}
