


















































































































































































































/**
 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";

@Component({
  components: {}
})
export default class MenuFuncManage extends Vue {
  @Prop({ type: [String, Number], default: "" })
  currentId!: string;

  @Prop({ type: Array, default: [] })
  funcData!: Array<any>;

  loading = false;
  // 添加功能
  addFuncDialog = false;
  tableForm = {
    name: "",
    code: "",
    navigationId: ""
  };
  allFuncTableData: Array<any> = [];
  total = 0;
  pageNum = 1;
  pageSize = 10;

  // 添加接口
  interForm = {
    name: "",
    uri: "",
    functionId: ""
  };
  interAddDialog = false;
  currentFuncId = "";
  interTabeleData: Array<any> = [];
  interPageNum = 1;
  interPageSize = 10;
  interTotal = 0;
  currentInterData: Array<any> = [];
  ownTnterNum = 0;
  currentInterTotal = 0;
  currentLoading = false;
  interDialog = false;

  // 添加功能
  handleAddFunc() {
    this.tableForm = {
      name: "",
      code: "",
      navigationId: this.currentId
    };
    this.pageNum = 1;
    this.pageSize = 10;
    this.getFuncData();
    this.addFuncDialog = true;
  }
  search() {
    this.pageNum = 1;
    this.getFuncData();
  }
  // 获取功能数据
  getFuncData() {
    this.loading = true;
    this.$http
      .post(Url.getUnSelectFuncList, {
        query: this.tableForm,
        current: this.pageNum,
        size: this.pageSize
      })
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.allFuncTableData = res.data.data.records;
          this.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }
  handleCurrentChange(val: number) {
    this.pageNum = val;
    this.getFuncData();
  }
  handleSizeChange(val: number) {
    this.pageSize = val;
    this.getFuncData();
  }
  // 添加功能
  handleFuncChoose(row: any, index: number) {
    this.$http
      .post(Url.addMenuFunc, {
        navigationId: this.currentId,
        operationCode: row.code
      })
      .then((res: any) => {
        if (res.data.status) {
          this.$message.success("添加成功");
          this.getFuncData();
          if (this.currentId) {
            this.$emit("update");
          }
        } else {
          this.$message.error(res.data.message);
        }
      });
  }
  // 删除功能
  handleDelFunc(row: any) {
    this.$confirm("确认删除该功能?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http.get(Url.deleteMenuFunc + "/" + row.id).then((res: any) => {
        if (res.data.status) {
          this.$message.success("删除成功");
          if (this.currentId) {
            this.$emit("update");
          }
        } else {
          this.$message.error(res.data.message);
        }
      });
    });
  }

  // 添加接口
  handleInterAdd(row: any) {
    this.currentFuncId = row.id;
    this.interForm = {
      name: "",
      uri: "",
      functionId: row.id
    };
    this.interPageNum = 1;
    this.ownTnterNum = 1;
    this.getInterData();
    this.getSelectInterData();
    this.interAddDialog = true;
  }
  interSearch() {
    this.interPageNum = 1;
    this.getInterData();
  }
  // 获取可选接口数据
  getInterData() {
    this.loading = true;
    this.$http
      .post(Url.getUnSelectInterList, {
        query: this.interForm,
        current: this.interPageNum,
        size: this.interPageSize
      })
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.interTabeleData = res.data.data.records;
          this.interTotal = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }
  handleInterCurrentChange(val: number) {
    this.interPageNum = val;
    this.getInterData();
  }
  // 获取已选接口数据
  getSelectInterData() {
    this.currentLoading = true;
    this.$http
      .post(Url.getSelectInterList, {
        query: {
          functionId: this.currentFuncId
        },
        current: this.ownTnterNum,
        size: this.interPageSize
      })
      .then((res: any) => {
        this.currentLoading = false;
        if (res.data.status) {
          this.currentInterData = res.data.data.records;
          this.currentInterTotal = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.currentLoading = false;
      });
  }
  handleOwnInterCurrentChange(val: number) {
    this.ownTnterNum = val;
    this.getSelectInterData();
  }
  // 添加接口
  handleInterChoose(row: any, index: number) {
    this.$http
      .post(Url.addMenuInter, {
        functionId: this.currentFuncId,
        interfaceId: row.id
      })
      .then((res: any) => {
        if (res.data.status) {
          this.$message.success("添加成功");
          this.getSelectInterData();
          this.getInterData();
          this.$emit("update");
        } else {
          this.$message.error(res.data.message);
        }
      });
  }
  // 删除接口
  handleInterDelete(row: any, index: number) {
    this.$confirm("确认删除该接口?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http
        .post(Url.deleteMenuInter, {
          functionId: this.currentFuncId,
          interfaceId: row.id
        })
        .then((res: any) => {
          if (res.data.status) {
            this.$message.success("删除成功");
            this.getSelectInterData();
            this.getInterData();
            this.$emit("update");
          } else {
            this.$message.error(res.data.message);
          }
        });
    });
  }

  // 查看已选接口
  interDetail(row: any) {
    this.currentFuncId = row.id;
    this.ownTnterNum = 1;
    this.getSelectInterData();
    this.interDialog = true;
  }
}
