













































































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";

@Component({})
export default class AddMenu extends Vue {
  @Prop({ type: String, default: "添加菜单" })
  dialogTitle?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  @Prop({ type: Object })
  attrForm!: object;

  @Prop({ type: Array })
  openOption!: any;

  @Watch("dialogVisible")
  handleVisible(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.$nextTick(() => {
        let ref: any = this.$refs.addForm;
        ref.resetFields();
        this.addForm = JSON.parse(JSON.stringify(this.attrForm));
      });
    }
  }

  addDialog = false;
  // 表单规则
  rules: any = {
    name: { required: true, message: "请输入名称", trigger: "blur" },
    code: { required: true, message: "请输入编码", trigger: "blur" },
    innerRoute: { required: true, message: "请选择路由", trigger: "blur" },
    path: { required: true, message: "请输入路由", trigger: "blur" }
  };
  addForm: any = {
    id: "",
    parentId: null,
    parentName: "",
    name: "",
    icon: "",
    code: "",
    innerRoute: true,
    path: "",
    target: "_self",
    remark: ""
  };
  loading = false;

  closeDialog() {
    this.$emit("close");
  }

  saveSensorAttribute() {
    let rest: any = this.$refs.addForm;
    rest.validate((valid: any) => {
      if (valid) {
        this.loading = true;
        let url = this.addForm.id ? Url.editMenu : Url.addMenu;
        if (typeof this.addForm.remark === "string") {
          this.addForm.remark = this.addForm.remark.trim();
        }
        this.$http
          .post(url, this.addForm)
          .then((res: any) => {
            this.loading = false;
            if (res.data.status) {
              this.$message.success(this.addForm.id ? "编辑成功" : "添加成功");
              this.$emit("submit");
            } else {
              this.$message({
                message: res.data.message,
                type: "error"
              });
            }
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        return false;
      }
    });
  }
}
